















import { Component, Vue, Prop } from 'vue-property-decorator';

@Component
export default class UnderlineForm extends Vue {
    @Prop({required: false, default:''})
    beforetext!: string;

    @Prop({required: false, default:''})
    aftertext!: string;

    @Prop({required: true})
    text!: string;

    @Prop({required: true})
    hint!: string;

    @Prop({required: true})
    textwidth!: string;

    @Prop({required: true, default:'#151515'})
    textColor!: string;
    
    @Prop({required: false, default:'11pt'})
    textSize!: string;
    
    @Prop({required: false, default:'300'})
    textWeight!: string;

    @Prop({required: false, default:'6pt'})
    hintFontSize!: string;
    
    @Prop({required:false, default:false})
    italicHint!: boolean;

    @Prop({default: '-15px'})
    marginTop!: string;
}
