})
export default class FormViLayout extends Vue {
    
    @Prop({required:true})
    copyType!: string;

    dataReady = false;
    pageId='';

    mounted(){
        this.dataReady = false;

        if(this.copyType.includes('ICBC') || this.copyType.includes('SUPERINTENDENT')){
            this.pageId="print"
        }else{
            this.pageId=this.copyType.replace(' ','').toLowerCase()
        }

        // this.extractInfo();
        this.dataReady = true;
    }

    // public extractInfo(){
           
